	module.exports = {"data":{"markdownRemark":{"html":"<h2>Information Architecture</h2>\n<p>Information Architecture (IA) is the practice of organizing a shared information environment, such as a website. Additionally, it is the starting point when creating a new product in which you must share information with others. </p>\n<p>IA encompasses the following:</p>\n<ul>\n<li>presenting</li>\n<li>searching</li>\n<li>browsing</li>\n<li>labeling</li>\n<li>categorizing</li>\n<li>sorting</li>\n<li>manipulating</li>\n<li>stratigically hiding information</li>\n</ul>\n<h2>Other Resources</h2>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Information_architecture\">Information architecture - Wikipedia</a></li>\n<li><a href=\"https://www.usability.gov/what-and-why/information-architecture.html\">Usabilty.gov</a></li>\n</ul>","fields":{"slug":"/user-experience-design/information-architecture/"},"frontmatter":{"title":"Information Architecture"}}},"pathContext":{"slug":"/user-experience-design/information-architecture/"}}

